.users img {
  height: 140px;
  background-color: white;
  box-shadow: 0px 0px 0px 0px black;
}

.users {
  display: inline-block;
  width: 140px;
  text-align: center;
  margin-left: 45px;
  margin-bottom: 20px;
  margin-top: 20px;
  box-shadow: 0 0 2.5px -1px black;
  word-wrap: break-word;
  border-radius: 8px;
  height: 210px;
  overflow: hidden;
  background-color: white;
}

