.users .name {
  font-size: 15px;
  overflow: hidden;
  word-wrap: break-word;
  display: inline-block;
}

.unfollowColor {
  background-color: rgb(188, 223, 132);
  border-radius: 3px;
  font-size: 13px;
  padding: 6px 10px 6px 10px;
  color: black;
}

.unfollowColor:hover {
  background-color: rgb(164, 202, 102);
}

.followColor {
  background-color: rgb(8, 107, 255, 0.8);
  border-radius: 3px;
  font-size: 13px;
  padding: 6px 10px 6px 10px;
}

.usersBlock {
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 0px 2.5px -1px black;
  border-radius: 10px;
}
