.Wrapper {
  width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
    "h h"
    "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 15px;
}

.Content {
  grid-area: c;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  margin-bottom: 20px;
}

a {
  text-decoration: none;
  color: black;
}

button {
  background-color: rgb(0, 0, 255, 0.6);
  border: none;
  color: white;
  cursor: pointer;
  padding: 5px;
  outline: none;
  font-weight: bold;
}

button:hover {
  background-color: #0096cc;
}

textarea {
  padding: 10px;
  background-color: #f4f7f8;
  border-radius: 10px;
  resize: none;
  border: none;
  outline: none;
}

::placeholder {
  padding-left: 2px;
}
input {
  background-color: #f4f7f8;
  border: none;
  padding: 5px;
  border-radius: 5px;
  outline: none;
}
.error {
  color: red;
  margin-top: 10px;
  margin-bottom: 10px;
}
