.MyPosts {
  border-radius: 10px;
  padding-top: 0px;
  float: right;
  width: 773px;
}

.userPosts {
  display: inline-block;
  background-color: tomato;
  padding: 0 10px 0 10px;
  border-radius: 4px;
  color: white;
}

.moiPosts {
  display: inline-block;
  background-color: rgb(104, 104, 255);
  padding: 0 10px 0 10px;
  border-radius: 4px;
  color: white;
}

.MyPosts .Publickbutton {
  padding: 5px;
}

.textareaButton {
  padding: 15px;
  background-color: white;
  border-radius: 10px;
  float: right;
  /* background-color: rgb(221, 239, 255); */
}

h2 {
  text-align: center;
}

.textareaButtonBlock {
  display: grid;
  grid-template-columns: 1fr 10fr;
  padding: 0 0px 0 10px;
  box-shadow: 0px 0px 2.5px -1px;
  border-radius: 10px;
  background-color: #fff;
  margin-bottom: 15px;
}

.textareaButton textarea {
  float: left;
  border: none;
  margin-top: 2px;
  background-color: rgb(228, 239, 248);
}

.Post {
  padding-bottom: 50px;
}

.buttonForm {
  margin-left: 10px;
  margin-top: 5px;
  padding: 14px 19px 14px 19px;
  font-size: 12px;
  border-radius: 10px;
}

.textareaButtonBlock img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  display: inline-block;
  margin-top: 15px;
  margin-left: 5px;
  box-shadow: 0px 0px 3.5px -1px;
}
