.Nav {
  grid-area: n;
  height: 130px;
  margin-bottom: 20px;
}

.item {
  font-size: 22px;
  padding-left: 5px;
  letter-spacing: 1px;
  display: inline-block;
  margin-left: 5px;
  padding: 5px;
  font-size: 17px;
  width: 115px;
  font-weight: bold;
  opacity: 0.7;
}

.activeLink {
  opacity: 1;
  padding: 5px 5px 5px 0px;
  border-radius: 6px 0px 0px 6px;
}

.Nav img {
  width: 17px;
  margin-left: 7px;
}

.items {
  margin-bottom: 9px;
  background-color: rgb(250, 247, 247);
  border-radius: 14px;
  box-shadow: 0 0 3px -1px black;
}

.items:hover {
  background-color: rgb(255, 255, 255);
}

.img {
  display: inline-block;
  background-color: #fff;
  padding: 5px 10px 5px 5px;
  border-radius: 14px 0 0 14px;
}
