.PostImg img {
  height: 70px;
  width: 70px;
  background-color: white;
  padding: 10px;
  border-radius: 70px;
  margin-bottom: 10px;
  margin-left: 10px;
}

.PostImg {
  text-align: center;
}

.PostImg button {
  padding-top: 10px;
}

.likesWord {
  font-weight: bold;
  opacity: 0.5;
}

.likesZone {
  padding: 5px 0 5px 0;
  width: 100%;
  border-radius: 0 0 10px 10px;
}

.delete {
  float: right;
  margin-right: 10px;
  margin-bottom: 5px;
}

.delete button {
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  color: black;
  padding: 7px 15px 7px 15px;
  font-size: 13px;
  margin-right: 10px;
  margin-bottom: 10px;
}

.delete button:hover {
  background-color: rgb(248, 248, 248);
  box-shadow: 0px 0px 2.5px -1px black;
}

.imgZone {
  /* box-shadow: 0px 0px 2.5px -1px; */
  background-color: #fff;
  margin-right: -15px;
  border-radius: 10px;
  text-align: center;
  height: 70px;
}

.likes {
  background-color: rgb(158, 247, 247);
  margin-left: 10px;
  border-radius: 4px;
  display: inline-block;
  margin-top: 0px;
  font-weight: bold;
  padding: 2px 7px 2px 7px;
  border-radius: 10px;
  opacity: 0.8;
}

.Posts {
  width: 772px;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 1fr 10fr;
  margin-bottom: 10px;
  background-color: #fff;
}

.text {
  padding: 20px;
  min-height: 200px;
}

.postMessage {
  display: inline-block;
  margin-top: 0;
  margin-left: 10px;
  border-radius: 10px;
  background-color: rgb(240, 240, 240);
  margin: 15px;
  font-size: 17px;
  word-break: break-all;
  /* box-shadow: 0px 0px 2.5px -1px black; */
  min-height: 200px;
}
