.AvaImg img {
  height: 200px;
  width: 200px;
  border-radius: 10px 10px 0 0;
  box-shadow: 0px 0px 0px 0px black;
}

.profileInfo {
  display: grid;
  grid-template-columns: 2fr 10fr;
  gap: 15px;
  background-color: #ebe8e8;
}

.AvaImg {
  height: 290px;
  box-shadow: 0px 0px 2.5px -1px black;
  border-radius: 10px;
  background-color: #fff;
  border-radius: 10px;
}

.profileForm {
  background-color: rgb(255, 255, 255);
  border-radius: 4px;
  padding: 10px 0 20px 30px;
  box-shadow: 0px 0px 2.5px -1px black;
  border-radius: 10px;
}

.userInfo {
  background-color: rgb(255, 255, 255);
  border-radius: 4px;
  padding: 20px;
  box-shadow: 0px 0px 2.5px -1px black;
  border-radius: 10px;
}

.userInfo div {
  padding: 5px;
  padding-top: 0px;
}

.userInfo .item {
  font-weight: bold;
  font-size: 17px;
  display: inline-block;
}

.infoGridsBlock hr {
  opacity: 0.2;
}

.serverStrings {
  opacity: 0.7;
  font-size: 17px;
}

.natiolStrings .serverStrings {
  padding: 5px 0px 2px 10px;
}

.natiolStrings .serverStrings:hover {
  background-color: rgb(223, 223, 223);
}

.status {
  display: inline-block;
  width: 460px;
  height: 22px;
  word-wrap: break-word;
  overflow: hidden;
  font-size: 15px;
  cursor: pointer;
}

.status .item {
  margin-right: 10px;
}

.statusitem {
  display: inline-block;
  padding: 0 0 10px 8px;
}
.statusitem:hover {
  background-color: rgb(238, 238, 238);
}

.infoGrids {
  display: grid;
  grid-template-columns: 3fr 10fr;
}

.contactsHead img {
  width: 20px;
  float: right;
  margin-top: 6px;
  margin-right: 10px;
}

.userName {
  font-size: 25px;
  font-weight: 5px;
  margin-bottom: 10px;
  display: inline-block;
}

.contacts {
  margin-left: 30px;
}

.contactsHead {
  margin-top: 32px;
  background-color: rgb(158, 247, 247);
  cursor: pointer;
  font-weight: bold;
  border-radius: 8px;
  width: 180px;
}

.contactsHead:hover {
  background-color: rgb(142, 238, 238);
}

.contactsHead span {
  display: inline-block;
  font-size: 18px;
  padding: 3px 0 2px 20px;
  border-radius: 5px;
  opacity: 1;
}

.addPhoto label {
  border-radius: 9px;
  box-shadow: 0px 0px 2.5px -1px black;
  background-color: rgb(0, 0, 255, 0.6);
  color: white;
  cursor: pointer;
  display: inline-block;
  margin-top: 35px;
  padding: 8px 3px 8px 3px;
  width: 160px;
  text-align: center;
  font-size: 17px;
  margin-left: 8px;
}

.addPhoto button {
  border-radius: 9px;
  box-shadow: 0px 0px 2.5px -1px black;
  background-color: rgb(0, 0, 255, 0.6);
  color: white;
  cursor: pointer;
  display: inline-block;
  margin-top: 35px;
  padding: 8px 3px 8px 3px;
  width: 160px;
  text-align: center;
  font-size: 17px;
  margin-left: 18px;
  font-weight: normal;
}

.addPhoto label:hover {
  background-color: rgb(0, 0, 255, 0.5);
}

.addPhoto button:hover {
  background-color: rgb(188, 223, 132);
  color: black;
}

.AvaImg input {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  z-index: -1;
}

.editButton {
  color: rgb(0, 60, 255);
  cursor: pointer;
  margin-right: 5px;
  float: right;
}

.menu {
  display: flex;
  margin-top: 20px;
}

.menu img {
  width: 25px;
  height: 25px;
  background-color: rgb(151, 185, 183, 0.5);
  padding: 7px;
  border-radius: 35%;
}

.menu .items {
  padding: 5px 5px 1px 5px;
  background-color: rgb(92, 155, 34);
  border-radius: 20px;
  margin-left: 14px;
}

.menu img:hover {
  background-color: rgb(151, 185, 183, 0.7);
  box-shadow: 0px 0px 2px 0px black;
}

.editButton:hover {
  color: black;
}

.formInputs {
  margin-bottom: 10px;
}

.profileForm hr {
  opacity: 0.3;
}

.profileForm button {
  padding: 4px 25px 4px 25px;
  font-weight: normal;
  border-radius: 6px;
}

.profileForm .item {
  color: black;
  display: inline-block;
  font-weight: bold;
  margin-bottom: 10px;
}

.profileForm label {
  float: right;
  margin-right: 20px;
  color: blue;
  cursor: pointer;
}

.profileForm label:hover {
  color: black;
}

.unfollow button {
  background-color: rgb(179, 212, 126);
  color: black;
}
