.h1 {
  margin: auto;
  font-size: 30px;
  padding-left: 10px;
  padding-top: 6px;
  display: inline-block;
  color: white;
  font-size: normal;
  margin-left: 30px;
}

.logBlock {
  float: right;
  padding-right: 10px;
