.pageNumberBold {
  cursor: pointer;
  opacity: 0.3;
  padding-left: 4px;
  font-weight: bold;
}

.pageNumber {
  padding: 10px;
  cursor: pointer;
  text-align: center;
}

.pageNumber button {
  margin-left: 20px;
  margin-right: 20px;
  border-radius: 10px;
  padding: 5px 15px 5px 15px;
  font-size: 12px;
}
